import React, { useState } from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import 'animate.css';
import { TimeList } from '../../App';
import { extractTimeInDate, getSegmentTime } from '../../Utils/convert';
import Tooltip from '@mui/material/Tooltip';
import Fade from '@mui/material/Fade';
import Zoom from '@mui/material/Zoom';
import Row from './Cell';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import styled from 'styled-components';
import TouchAppIcon from '@mui/icons-material/TouchApp';

const IconWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`;

export default function BasicTable({ timeList }: { timeList: TimeList[] }) {
  return (
    <TableContainer component={Paper}>
      <Table
        sx={{
          minWidth: 300,
          maxWidth: 1000,
          margin: '0 auto',
        }}
        aria-label="simple table"
        stickyHeader
      >
        <TableHead>
          <TableRow>
            <TableCell sx={{ width: '10%' }} align="center">
              <IconWrapper>
                <InfoOutlinedIcon />
              </IconWrapper>
            </TableCell>
            <TableCell sx={{ width: '30%' }} align="center">
              
            </TableCell>
            <TableCell sx={{ width: '30%' }} align="center">
              
            </TableCell>
            <TableCell sx={{ width: '30%' }} align="center">
               
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {timeList.map((el, index) => (
            <Row key={index} el={el} index={index} />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
