import { TimeList } from '../App';

// [{start: 'Date.now().toString()'}]
export const convertDateObj = (timeNumber: string) => {
  return new Date(parseInt(timeNumber));
};

export const extractTimeInDate = (timeNumber: string) => {
  return convertDateObj(timeNumber).toTimeString().substring(0, 8);
};

const checkHowManyDigit = (time: number): string => {
  return time < 10 ? `0${time}` : `${time}`;
};

export const getSegmentTime = (date: TimeList) => {
  // start end       end - start     duration 
  if (date.end === undefined) return;
  const duration = parseInt(date.end) - parseInt(date.start);
  const newSegmentTime = new Date(duration)
    .toUTCString()
    .toString()
    .slice(17, 25);

  return `${newSegmentTime}`;
};
