import { createContext, useState } from 'react';
import { TimeList } from './App';

interface Props {
  timeList: TimeList[];
  setTimeList: React.Dispatch<React.SetStateAction<TimeList[]>>;
}

export const TimeListContext = createContext<Props>({
  timeList: [],
  setTimeList: () => {},
});

export function TimeListProvider({ children }: { children: React.ReactNode }) {
  const [timeList, setTimeList] = useState<TimeList[]>([]);

  return (
    <TimeListContext.Provider value={{ timeList, setTimeList }}>
      {children}
    </TimeListContext.Provider>
  );
}
