import { TableCell, TableRow } from '@mui/material';
import React, { useState } from 'react';
import { TimeList } from '../../../App';
import { extractTimeInDate, getSegmentTime } from '../../../Utils/convert';
import FormDialog from '../../Dialog';

interface Props {
  el: TimeList;
  index: number;
}

function Row({ el, index }: Props) {
  const [dialogOpen, setDialogOpen] = useState(false);

  const handleDialogClose = () => {
    setDialogOpen(false);
    console.log('handleDialogClose !');
  };

  const handleDialogOpen = () => {
    setDialogOpen(true);
  };

  return (
    <TableRow
      key={el.start}
      sx={{
        '& td, & th': {
          border: '0',
        },
        '& td:not(:first-of-type)': {
          padding: '12px',
        },
      }}
      data-testid={index}
      className="animate__animated animate__fadeInDown"
    >
      <TableCell align="center" scope="row" sx={{ padding: 0 }}>
        <FormDialog
          index={index}
          dialogOpen={dialogOpen}
          handleDialogClose={handleDialogClose}
          handleDialogOpen={handleDialogOpen}
          element={el}
        />
      </TableCell>
      <TableCell align="center">{extractTimeInDate(el.start)}</TableCell>
      <TableCell align="center">
        {el?.end ? extractTimeInDate(el.end) : ''}
      </TableCell>
      <TableCell align="center">{el?.end ? getSegmentTime(el) : ''}</TableCell>
    </TableRow>
  );
}

export default Row;
