import React, { useCallback, useContext, useEffect, useState } from 'react';
import './App.css';
import styled from 'styled-components';
import Button from './Components/Button';
import Table from './Components/Table/index';
import 'animate.css';
import { Grid } from '@mui/material';
import CssBaseline from '@mui/material/CssBaseline';
import { TimeListContext } from './context';

const ButtonContainer = styled.div``;

const ButtonGroup = styled.div`
  display: flex;
  justify-content: center;
  /* width: 100%; */
  z-index: 1;
  /* background-color: rgba(120, 40, 212, 0.7); */
  Button {
    margin: 10px 20px;
  }
`;

export interface TimeList {
  start: string;
  end?: string | undefined;
  category?: string;
}

function App() {
  // const [timeList, setTimeList] = useState<TimeList[]>([]);
  const [isStart, setIsStart] = useState(true);
  const { timeList, setTimeList } = useContext(TimeListContext);

  // localStorage   
  function setTimeListInDB(timeList: TimeList[], isStart: boolean) {
    localStorage.setItem('TimeList', JSON.stringify(timeList));
    localStorage.setItem('startState', JSON.stringify(isStart));
  }

  // localStorage   
  function parseJsonData(data: string) {
    return JSON.parse(data);
  }

  function getTimeListInDB() {
    const list = localStorage.getItem('TimeList');
    const state = localStorage.getItem('startState');
    if (list === null || state === null) {
      return '';
    }
    return {
      list: parseJsonData(list),
      state: parseJsonData(state),
    };
  }

  // localStorage 
  function resetTimeListInDB() {
    localStorage.setItem('TimeList', JSON.stringify([]));
    localStorage.setItem('startState', JSON.stringify(true));
  }

  const onClick = useCallback(() => {
    const prevList = [...timeList];
    let newList: TimeList[];
    let newStartState: boolean;
    if (isStart) {
      newList = [...prevList, { start: Date.now().toString(), end: undefined }];
      newStartState = false;
    } else {
      prevList[prevList.length - 1].end = Date.now().toString();
      newList = prevList;
      newStartState = true;
    }
    setTimeList(newList);
    setIsStart(newStartState);
    setTimeListInDB(newList, newStartState);
  }, [isStart, timeList]);

  const onReset = () => {
    setTimeList([]);
    setIsStart(true);
    resetTimeListInDB();
  };

  // useEffect(() => {
  //   const onSpacePress = (e: KeyboardEvent) => {
  //     if (e.code === 'Space') {
  //       e.preventDefault();
  //       onClick();
  //     }
  //   };

  //   window.addEventListener('keydown', onSpacePress);
  //   return () => window.removeEventListener('keydown', onSpacePress);
  // }, [isStart, onClick]);

  useEffect(() => {
    const parsedList = getTimeListInDB();
    if (parsedList) {
      const { list, state } = parsedList;
      setTimeList(list);
      setIsStart(state);
    }
  }, []);

  return (
    <>
      <CssBaseline />
      <Grid container mb={3} height={'80vh'} sx={{ overflowY: 'scroll' }}>
        <Table timeList={timeList} />
      </Grid>
      <ButtonContainer>
        <ButtonGroup>
          {isStart ? (
            <Button onClick={onClick} backgrouncColor="#db1010" text="" />
          ) : (
            <Button onClick={onClick} backgrouncColor="#1143e9" text="" />
          )}
          <Button onClick={onReset} backgrouncColor="#3a3838" text="" />
        </ButtonGroup>
      </ButtonContainer>
    </>
  );
}

export default App;
